import React, { useEffect } from "react";
import { useSelector } from "react-redux";

//components
import LayoutBox from "../../common/layoutBox";
import CalendarHeader from "./calendarHeader";
import CalendarDay from "./calendarDay";
import CalendarCell from "./calendarCell";

//custom hooks
import useGetContent from "../../../hooks/list/useGetContent";


function Calendar() {
  const currentMonth = useSelector( state => state.currentMonth)
  const { getContent }  = useGetContent()

  // currentDate     useEffect()
  useEffect(()=>{
    getContent()
  },[currentMonth])


  return (
    <LayoutBox basis="80%" direction="column" justify="start">
      {/*  */}
      <CalendarHeader/>
      {/*  */}
      <CalendarDay/>
      {/*  */}
      <CalendarCell/>
    </LayoutBox>
  )
}

export default Calendar